                    <p>Tambin conocido como Golang, Go fue desarrollado por Google para ser un lenguaje eficiente, legible y seguro para la programacin a nivel de sistema. Funciona bien para sistemas distribuidos, en los que los sistemas estn ubicados en diferentes redes y necesitan comunicarse enviando mensajes entre s. Si bien es un lenguaje relativamente nuevo, Go tiene una gran biblioteca de estndares y una extensa documentacin.</p>
